import React, { useRef } from 'react';
import './Checkbox.scss';

/**
 *  Checkbox    .
 *
 * @param {string} className -  CSS     .
 * @param {string} name -  .
 * @param {function} handleChangeCheckbox -        .
 * @param {boolean} checked -   .
 * @param children -   .
 * @returns {JSX.Element} -  JSX-  Checkbox.
 * @author   | https://t.me/Chia_Rio_Ru
 */
const Checkbox = ({ className, name, handleChangeCheckbox, children, checked = false }) => {
  const handleClick = (event) => {
    if (handleChangeCheckbox && event.target.tagName.toLowerCase() !== 'input')
      handleChangeCheckbox(event);
  };
  return (
    <label onClick={handleClick} className={`checkbox ${className || ''}`}>
      <input
        className="checkbox__input"
        type="checkbox"
        name={name}
        checked={checked}
        onChange={() => {}}
      />
      <span className={`checkbox__checkbox ${checked ? 'checkbox__checkbox_active' : ''}`}></span>
      {children}
    </label>
  );
};

export default Checkbox;
